<template>
  <nav class="navbar navbar-expand-lg navbar-light bg-light">
    <div class="container">
      <router-link to="/" class="navbar-brand">HoB Trade</router-link>
      <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="collapse navbar-collapse" id="navbarSupportedContent">
        <ul class="navbar-nav me-auto mb-2 mb-lg-0">
          <li class="nav-item dropdown">
            <a class="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
              Setting
            </a>
            <ul class="dropdown-menu p-2" aria-labelledby="navbarDropdown">
              <li><router-link to="/setting" class="nav-link ml-1">General</router-link></li>
              <li><hr class="dropdown-divider"></li>
              <li><router-link to="/setting-secure" class="nav-link">Secure</router-link></li>
              <li><router-link to="/setting-trades" class="nav-link">Trades</router-link></li>
            </ul>
          </li>
          <li class="nav-item">
            <router-link to="/trade-history" class="nav-link">History</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/about" class="nav-link">About</router-link>
          </li>
        </ul>
        <ul class="navbar-nav pull-right">
          <li class="nav-item dropdown">
            <a class="nav-link dropdown-toggle" href="#" id="userDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
              Hi, {{ user?.name }}
            </a>
            <ul class="dropdown-menu p-2" aria-labelledby="userDropdown">
              <li><a class="nav-link" href="#">Preferences</a></li>
              <li><a class="nav-link" @click="logout" href="javascript:void()">Logout</a></li>
            </ul>
          </li>
        </ul>
      </div>
    </div>
    <div class="modal fade" id="jwtModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">JWT Token settings</h5>
            <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
          </div>
          <div class="modal-body">
            <p>Please input JWT token by access <a :href="jwtUrl" target="_blank">Token page</a></p>
            <div class="form-group">
              <input class="form-control" type="text" placeholder="Token" v-model="jwtToken">
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
            <button type="button" @click="saveToken" class="btn btn-primary">Save</button>
          </div>
        </div>
      </div>
    </div>
  </nav>
</template>

<script>
import global from '@/global'
import { Modal } from 'bootstrap'
export default {
  name: 'AppHeader',
  data: function () {
    return {
      jwtModal: null,
      user: null,
      jwtUrl: global.getRemote('/user/token'),
      jwtToken: '',
      saveToken: function () {
        this.jwtModal.hide()
        if (this.jwtToken !== '') {
          global.jwt = this.jwtToken
          localStorage.setItem('jwt', this.jwtToken)
          this.loadUser()
        }
      },
      loadUser: async function () {
        const userStore = localStorage.getItem('user')
        if (userStore) {
          this.user = JSON.parse(userStore)
        }
        const data = global.parseJwt(this.jwtToken)
        if (data.exp < Math.round((new Date()).getTime() / 1000)) {
          this.logout()
        }
        this.user = { id: data.i, email: data.e, name: data.n, role: data.r }
        localStorage.setItem('user', JSON.stringify(this.user))
      },
      logout: function () {
        localStorage.removeItem('jwt')
        localStorage.removeItem('user')
        location.reload()
      }
    }
  },
  mounted: function () {
    this.jwtToken = localStorage.getItem('jwt')
    if (!this.jwtToken || this.jwtToken === '') {
      this.jwtModal = new Modal(document.getElementById('jwtModal'), {})
      this.jwtModal.show()
    } else {
      global.jwt = this.jwtToken
      this.loadUser()
    }
  }
}
</script>
